@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/OpenSans-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/OpenSans-Light.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/OpenSans-Italic.woff") format("woff");
}
@font-face {
  font-family: "Clear Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/ClearSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "Clear Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/ClearSans-Bold.woff") format("woff");
}
@font-face {
  font-family: "Clear Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/ClearSans-Light.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  box-sizing: border-box;
  padding: 16px;
  font-family: "Clear Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 28px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  letter-spacing: 1px;
  background-color: #5b6460;
  border: none;
  outline: none;
  cursor: pointer;
}
.button:hover, .button:active {
  background-color: #808e88;
}
.button:active {
  opacity: 0.7;
}
.button:disabled {
  opacity: 0.7;
  pointer-events: none;
}
.button--big {
  font-size: 18px;
}

@media (max-width: 800px) {
  .button {
    padding: 10px;
    font-size: 12px;
  }
  .button--big {
    padding: 12px;
    font-size: 14px;
  }
}
.page {
  min-width: 100%;
  max-width: 100%;
}

.page__header {
  width: 100%;
  box-shadow: 0 2px 20px 0 rgba(0, 0, 0, 0.08);
}

.page__body {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  min-width: 372px;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 187%;
  color: #333333;
}

.page__main {
  flex-grow: 1;
}

.page__payment {
  max-width: 1408px;
  width: 100%;
  margin: 0 auto;
}

@media (max-width: 800px) {
  .page__body {
    font-size: 12px;
    line-height: 22px;
  }
}
.main-header {
  position: relative;
  z-index: 3;
  box-sizing: border-box;
  width: 100%;
  max-width: 1408px;
  margin: 0 auto;
  padding: 20px 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-header__logo-link {
  margin-left: calc(30px + 6vw);
  margin-right: 10px;
}
.main-header__logo-link:hover .main-header__logo {
  filter: drop-shadow(0 2px 2px #5b6460);
}
.main-header__logo-link:active .main-header__logo {
  filter: drop-shadow(0 2px 2px #5b6460);
  opacity: 0.7;
}

.main-header__logo {
  display: block;
  flex-shrink: 0;
  margin: 0;
  padding: 0;
  width: 70px;
  height: auto;
}

.main-header__nav-wrapper {
  display: flex;
  align-items: center;
}

.main-header__pseudo-select {
  align-self: center;
  padding-top: 3px;
}

.main-header__language {
  display: none;
  margin: 0;
  padding: 16px 0 12px 0;
  background: transparent;
  border: none;
  font-family: "Clear Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 28px;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
}
.main-header__language:hover {
  opacity: 0.7;
  cursor: pointer;
}

.main-header__booking-link {
  margin: 0;
  margin-left: 10px;
  width: 200px;
  min-height: 60px;
  flex-shrink: 1;
}

.main-header__menu-toggler {
  display: none;
}

@media (max-width: 800px) {
  .main-header {
    padding: 10px;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.08);
  }
  .main-header__logo-link {
    margin: 0;
  }
  .main-header__logo {
    width: 60px;
  }
  .main-header__nav-wrapper {
    box-sizing: border-box;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 25px;
    background-color: rgba(255, 255, 255, 0.9);
  }
  .main-header__nav-wrapper--closed {
    display: none;
  }
  .main-header__nav-wrapper--opened {
    display: flex;
  }
  .main-header__booking-link {
    width: 150px;
    min-height: 40px;
    margin: 0;
  }
  .main-header__menu-toggler {
    position: relative;
    display: block;
    width: 40px;
    height: 40px;
    border: none;
    background: transparent;
    cursor: pointer;
  }
  .main-header__menu-toggler--burger::before {
    position: absolute;
    top: 50%;
    left: 0;
    content: "";
    width: 100%;
    height: 2px;
    background-color: #5b6460;
    box-shadow: 0 10px 0 0 #5b6460, 0 -10px 0 0 #5b6460;
    transform: translateY(50%);
  }
  .main-header__menu-toggler--cross::before, .main-header__menu-toggler--cross::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 2px;
    display: block;
    background-color: #5b6460;
  }
  .main-header__menu-toggler--cross::before {
    transform: rotate(45deg);
  }
  .main-header__menu-toggler--cross::after {
    transform: rotate(-45deg);
  }
  .main-header__menu-toggler:hover {
    opacity: 0.7;
  }
  .main-header__menu-toggler:active {
    opacity: 0.4;
  }
}
.main-nav {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
  list-style-type: none;
}

.main-nav__item {
  margin: 0;
  margin-right: 15px;
  padding: 10px;
}

.main-nav__link {
  margin: 0;
  padding: 10px 0;
  font-family: "Clear Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
}
.main-nav__link:hover, .main-nav__link:active {
  text-decoration: underline;
}
.main-nav__link:active {
  opacity: 0.4;
}

@media (max-width: 800px) {
  .main-nav__list {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .main-nav__item {
    margin: 0;
  }
}
.pseudo-select {
  position: relative;
}

.pseudo-select__options-list {
  position: absolute;
  top: 100%;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
}
.pseudo-select__options-list--closed {
  display: none;
}

.pseudo-select__button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 2px 5px;
  box-sizing: border-box;
  background-color: #ffffff;
  border: none;
  box-shadow: none;
  outline: none;
  cursor: pointer;
}
.pseudo-select__button:hover, .pseudo-select__button:focus {
  opacity: 0.7;
  background-color: #dcdcdc;
  outline: none;
}
.pseudo-select__button:active {
  opacity: 0.4;
}

.footer {
  background-color: #5b6460;
  color: #ffffff;
}

.footer-wrapper {
  box-sizing: border-box;
  max-width: 1408px;
  margin: 0 auto;
  padding: 7vw 10vw;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.footer__subscription-label {
  display: block;
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  font-weight: 600;
  font-size: 16px;
  line-height: 30px;
}

.footer__input-wrapper {
  display: flex;
  box-sizing: border-box;
  width: 100%;
  max-width: 432px;
  border-bottom: 1px solid #efefef;
}
.footer__input-wrapper:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
.footer__input-wrapper:focus-within {
  outline: 1px solid #ffffff;
}

.footer__subscription-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 0;
  border: none;
  background: transparent;
  color: #efefef;
  cursor: pointer;
}
.footer__subscription-button:hover {
  background-color: rgba(255, 255, 255, 0.4);
  color: #5b6460;
}
.footer__subscription-button:active {
  opacity: 0.4;
}

.footer__subscription-input {
  display: block;
  flex-grow: 1;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: italic;
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  color: #efefef;
  background: transparent;
  border: none;
}
.footer__subscription-input:focus {
  border: none;
  outline: none;
}

.footer__text {
  width: 100%;
  max-width: 420px;
  font-weight: 300;
  font-size: 16px;
  line-height: 30px;
  color: #ffffff;
  opacity: 0.7;
}

.footer__copyright {
  font-weight: 300;
  font-size: 14px;
  line-height: 30px;
  color: #efefef;
  opacity: 0.7;
}

.footer__links {
  place-self: start end;
}

.footer__links-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin: 0;
  margin-bottom: 50px;
  padding: 0;
  list-style: none;
}

.footer__links-item {
  margin-left: 32px;
}
.footer__links-item:first-child {
  margin-left: 0;
}

.footer__link {
  font-family: "Clear Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
}
.footer__link:hover {
  text-decoration: underline;
}
.footer__link:active {
  opacity: 0.5;
}

.footer__payment-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin: 0;
  margin-left: auto;
  padding: 0;
  list-style: none;
}

.footer__payment-item {
  margin-left: 40px;
}

@media (min-width: 1408px) {
  .footer-wrapper {
    box-sizing: border-box;
    max-width: 1408px;
    margin: 0 auto;
    padding: 100px 145px;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 800px) {
  .footer-wrapper {
    grid-template-columns: 1fr;
    padding: 40px;
    gap: 20px;
  }
  .footer__input-wrapper {
    width: 100%;
  }
  .footer__links {
    place-self: start;
  }
  .footer__links-list {
    justify-content: flex-start;
    margin-bottom: 20px;
  }
  .footer__links-item {
    margin: 0;
    margin-right: 15px;
  }
  .footer__payment-list {
    justify-content: flex-start;
  }
  .footer__payment-item {
    margin: 0;
    margin-right: 15px;
  }
  .footer__link {
    font-size: 12px;
  }
}
.main-index {
  width: 100%;
}

.main-index__header {
  width: 100%;
  max-height: 700px;
}

.main-index__section {
  box-sizing: border-box;
  width: 100%;
  max-width: 1408px;
  margin: 0 auto;
}

.main-index__about {
  position: relative;
}

.main-index__about-wrapper {
  position: relative;
  display: flex;
  width: 100%;
  max-height: 559px;
  height: 40vw;
  overflow: hidden;
}

.main-index__about-image {
  display: block;
  margin: 0;
  padding: 0;
  width: 50%;
  height: auto;
  object-fit: cover;
  flex-grow: 0;
}

.main-index__about-link {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  width: 157px;
  height: 157px;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 13px;
  border-radius: 50%;
  background-color: #e9ece0;
  font-family: "Clear Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  text-decoration: none;
  color: #333333;
  transform: translate(-50%, -50%);
}
.main-index__about-link:hover, .main-index__about-link:active {
  background-color: #5b6460;
  color: #ffffff;
  text-decoration: underline;
}
.main-index__about-link:active {
  opacity: 0.5;
}

.main-index__contacts {
  padding: 10.7vw 10vw 9.5vw 10vw;
}

@media (min-width: 1408px) {
  .main-index__contacts {
    padding: 150px 146px 133px 146px;
  }
}
@media (max-width: 800px) {
  .main-index__about-link {
    width: 100px;
    height: 100px;
  }
  .main-index__contacts {
    padding: 7vw 6vw 5vw 6vw;
  }
}
.slideshow {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
}

.slideshow__pictures {
  position: relative;
  overflow: hidden;
  width: 100%;
}

.slideshow__image {
  display: block;
  width: 100%;
  object-fit: cover;
  object-position: center;
}
.slideshow__image--index {
  width: 100%;
  height: 100%;
}
.slideshow__image--hidden {
  display: none;
}

.slideshow__controls {
  box-sizing: border-box;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  padding-top: 3vw;
  color: #333333;
  background-color: #ffffff;
}
.slideshow__controls--index {
  position: absolute;
  width: 54%;
  bottom: 10%;
  left: 46%;
  align-items: center;
  padding: 0 32px;
  color: #ffffff;
  background: transparent;
  transform: translate(0);
}

.slideshow__buttons {
  width: 115px;
  display: flex;
  justify-content: space-between;
}

.slideshow__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;
  margin: 0;
  padding: 0;
  cursor: pointer;
  color: inherit;
  background: transparent;
  border: none;
}
.slideshow__button:hover {
  filter: drop-shadow(0 0 5px #5b6460);
}
.slideshow__button:active {
  opacity: 0.5;
}
.slideshow__button:disabled {
  color: #dadada;
  pointer-events: none;
}
.slideshow__button--prev {
  transform: rotate(180deg);
}
.slideshow__button--index:hover {
  filter: drop-shadow(0 0 5px #ffffff);
}
.slideshow__button--index:disabled {
  opacity: 0.5;
}

@media (min-width: 1408px) {
  .slideshow__controls {
    padding-top: 40px;
  }
}
@media (max-width: 800px) {
  .slideshow__controls {
    transform: translateX(0);
  }
  .slideshow__controls--index {
    position: absolute;
    width: 100%;
    min-width: 372px;
    bottom: 15%;
    left: 0;
    padding: 0 20px;
  }
}
.index-header {
  position: relative;
  height: 50vw;
  padding-bottom: calc(111px - 1vw);
}

.index-header__slider {
  height: 100%;
  max-height: 700px;
  background-color: #5b6460;
}

.index-header__slider-inner {
  aspect-ratio: auto;
}
.index-header__slider-inner::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #5b6460;
  opacity: 0.35;
}

.index-header__container {
  position: absolute;
  box-sizing: border-box;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 6.8vw 4vw 0 0;
  color: #ffffff;
}

.index-header__title {
  margin: 0;
  margin-bottom: 34px;
  padding: 0;
  align-self: flex-end;
  font-weight: 300;
  font-size: calc(29px + 3vw);
  line-height: 103%;
  text-align: right;
}

.index-header__subtitle {
  align-self: flex-end;
  max-width: 340px;
  margin: 0;
  padding: 0 84px;
  font-weight: 400;
  font-size: calc(6px + 1vw);
  line-height: 125%;
  text-align: center;
}

.index-header__book {
  max-width: 383px;
  margin-left: 10vw;
  transform: translateY(-63%);
}

@media (min-width: 1408px) {
  .index-header {
    padding-bottom: 111px;
  }
}
@media (max-width: 800px) {
  .index-header {
    padding-bottom: 50px;
  }
  .index-header__title {
    margin-bottom: 20px;
    font-size: calc(14px + 3vw);
  }
  .index-header__subtitle {
    max-width: 200px;
    padding: 0 20px;
  }
  .index-header__book {
    margin: 0;
    width: 100%;
    max-width: none;
    transform: translateY(-50%);
  }
}
.pagination {
  display: flex;
  align-items: center;
  font-size: 17px;
  line-height: 19px;
}
.pagination--index {
  font-weight: 400;
  color: #ffffff;
}

.pagination__min,
.pagination__max {
  margin: 0;
  padding: 0;
}

.pagination__min {
  margin-right: 8px;
}

.pagination__track {
  position: relative;
  width: 88px;
  height: 2px;
  margin-right: 8px;
  background-color: #dcdcdc;
}
.pagination__track--index {
  background-color: rgba(255, 255, 255, 0.28);
}

.pagination__slider {
  position: absolute;
  width: 16.7%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #333333;
}
.pagination__slider--index {
  background-color: #ffffff;
}

.book-button {
  position: relative;
  display: flex;
}

.book-button-link {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70%;
  padding: calc(8px + 2vw);
  flex-shrink: 0;
}

.book-button__description {
  box-sizing: border-box;
  width: 30%;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #e9ece0;
  font-family: "Clear Sans", "Arial", sans-serif;
  font-weight: 400;
  flex-shrink: 0;
}

.book-button__from,
.book-button__per-element {
  padding: 0;
  margin: 0;
  font-size: calc(6px + 0.6vw);
  line-height: 18px;
}

.book-button__per-element {
  text-align: right;
}

.book-button__price {
  padding: 0;
  margin: 0;
  font-weight: 700;
  font-size: calc(6px + 1vw);
  line-height: 140%;
  text-align: center;
}
.book-button__price::first-letter {
  font-weight: 400;
}

@media (max-width: 800px) {
  .book-button {
    width: 100%;
  }
  .book-button-link {
    width: auto;
    flex-grow: 1;
  }
}
.cabins {
  box-sizing: border-box;
  width: 100%;
  max-width: 1408px;
  padding-bottom: 9.6vw;
}

.cabins__description-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 10.7vw;
}

.cabins__description {
  padding: 6.2vw 7.5vw 0 10.5vw;
}

.cabins__title {
  margin: 0;
  margin-bottom: 3.5vw;
  padding: 0;
  font-weight: 400;
  font-size: 40px;
  line-height: 40px;
  color: #5b6460;
}

.cabins__text {
  margin: 0;
  margin-bottom: 1.3vw;
  padding: 0;
  color: #000000;
}

.cabins__link {
  position: relative;
  margin: 0;
  padding: 0;
  font-weight: 600;
  color: #5b6460;
  text-decoration: none;
  border-bottom: 2px solid #5b6460;
}
.cabins__link:hover {
  opacity: 0.7;
}
.cabins__link:active {
  opacity: 0.4;
}

.cabins__image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cabins__gallery {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 27vw;
  gap: 24px;
  padding: 0 33px;
}

@media (min-width: 1408px) {
