  border-radius: 0;
}
.payment__textarea:focus + .payment__label, .payment__textarea:not(:placeholder-shown) + .payment__label {
  font-size: 8px;
  line-height: 225%;
  top: 5px;
  transform: translateY(0);
}
.payment__textarea:hover, .payment__textarea:focus {
  border-color: #000000;
  box-shadow: 0 0 5px 0 #5b6460;
}
.payment__textarea:focus {
  outline: none;
}

.payment__terms {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-bottom: 50px;
}

.payment__terms-mark {
  position: relative;
  display: block;
  flex-shrink: 0;
  margin: 0;
  margin-right: 20px;
  padding: 0;
  width: 34px;
  height: 34px;
  background-color: #ffffff;
  border: 0.7px solid #5b6460;
  border-radius: 50%;
}
.payment__terms-mark::before, .payment__terms-mark::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  box-sizing: border-box;
  display: block;
  width: 15px;
  height: 1px;
  background-color: #333333;
  transform: translate(-50%, -50%);
}
.payment__terms-mark::after {
  transform: translate(-45%, -40%) rotate(90deg);
}

.payment__terms-input:checked + .payment__terms-mark {
  background-color: #5b6460;
}
.payment__terms-input:checked + .payment__terms-mark::before {
  width: 17px;
  height: 9px;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-top: none;
  border-right: none;
  transform: translate(-50%, -70%) rotate(-45deg);
}
.payment__terms-input:checked + .payment__terms-mark::after {
  display: none;
}

.payment__terms-name {
  display: block;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 175%;
}

.payment__terms-link {
  font-weight: 700;
  font-size: 16px;
  line-height: 175%;
  text-decoration-line: underline;
  color: #5b6460;
}
.payment__terms-link:hover {
  filter: drop-shadow(0 0 3px #5b6460);
}
.payment__terms-link:active {
  opacity: 0.5;
}

.payment__submit {
  width: 384px;
}

@media (min-width: 1408px) {
  .payment {
    padding: 100px 32px 150px 145px;
  }
  .payment__form {
    column-gap: 138px;
  }
  .payment__go-back {
    margin-bottom: 55px;
  }
  .payment__section {
    padding-bottom: 100px;
  }
  .payment__section--additional {
    padding-bottom: 50px;
  }
}
@media (max-width: 800px) {
  .payment {
    padding: 5vw;
    padding-bottom: 10vw;
  }
  .payment__form {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  .payment__total {
    border: none;
  }
  .payment__submit {
    width: 100%;
  }
}